import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import "./Movie.css";

// component가 state가 필요 없을 경우에는 class component가 될 필요X
function Movie({ id, year, title, summary, poster, genres, language, rating, booking, audience, ranking }){
    return (
        <div className="movie">
            <Link className="movie__link" to={{
            pathname: `/movie/${id}`,
            state: { year, title, summary, poster, genres }}}>
                {/* <label htmlFor= //in js, 'for' means loop */}
                <div className="movie__ranking">{ranking}</div>
                <img src={poster} alt={title} title={title} />
                <div className="movie__data">
                    <h4 className="movie__title">{title}</h4>
                    <h5 className="movie__year">{year} • {language}</h5>
                    <h5 className="movie__rating">예상★{Math.floor((rating*0.5)*10)/10}</h5>
                    <h6 className="movie__book">예매율{booking}% • 누적 관걕 {audience}만명</h6>
                </div>
            </Link>
        </div>
    );
}

Movie.protoTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
    rating: PropTypes.number.isRequired,
    language: PropTypes.string.isRequired,
    booking: PropTypes.number.isRequired,
    audience: PropTypes.number.isRequired,
    ranking: PropTypes.number.isRequired
};

export default Movie;